import React, { useState, useEffect } from "react";
import Loading from "./Loading";
import PropTypes from 'prop-types'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

const Container = styled.ul`
    max-width:50%;
    margin: 0 auto;
    padding-top: 50px;
    @media screen and (max-width:768px){
    padding-top: 10px;
    max-width:100%;
    }
`
const ListItem = styled.li`
    font-size:18px;
    color:white;
    padding:5px 0;
    display:flex;
    justify-content:center;
    width:100%;
    margin:auto;
    @media screen and (max-width:768px){
        width:90%;
        margin:auto
    }
`
const Rank = styled.div`
    font-size:20px;
    font-weight:700;
    flex:1;
    text-align:center;
    border:1px solid white;
    padding:5px;
    @media screen and (max-width:768px){
        flex:unset;
        font-size:16px;
        width:50px;
        padding:0;
    }
`
const Contents = styled(Link)`
    flex:6;
    border:1px solid white;
    border-left:0px;
    text-align:center;
    padding:5px;
    background:rgba(255,255,255,0.4);
    font-weight:500;
    color:white;
    @media screen and (max-width:768px){
        flex:unset;
        font-size:16px;
        width:100%;
        padding:0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

`
const CoinsComponent = ({ loading, error, preData }) => {
    const [isFetchMore, setIsFetchMore] = useState(false)
    const [preSet, setPreSet] = useState([])
    const [data, setData] = useState([])

    useEffect(() => {
        setPreSet(preData)
        const filterdData = preSet && preSet.filter((i, index) => index < 30 && i)
        setData(filterdData)
    }, [preData, preSet])

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY + document.documentElement.clientHeight > document.documentElement.scrollHeight - 20) {
                if (preSet.length <= data.length) {
                    return
                }
                else {
                    const filterdData = preSet && preSet.filter((i, index) => index < (data.length + 30) && i)
                    setIsFetchMore(true)
                    setData(filterdData)
                }
            }
        }
        window.addEventListener('scroll', handleScroll)
        return () => window.removeEventListener('scroll', handleScroll)
    }, [preSet, data])
    return (
        <>
            {loading ? <Loading message="Coins Loading..." /> : error ? <Loading message="You've got the Error!!" /> :
                <>
                    <Container>{
                        data && data.length > 0 && data.map(i =>
                            <ListItem key={i.id}>
                                <Rank>#{i.rank}</Rank>
                                <Contents to={`/coins/${i.id}`}>{i.name}/{i.symbol}</Contents>
                            </ListItem>
                        )
                    }</Container>
                    {isFetchMore ? <Loading top='-100px' size='3em' message="I'm the Infinity Scroll!!s" /> : null}
                </>
            }
        </>
    )
}

CoinsComponent.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}

export default CoinsComponent