import React, { useState, useEffect } from 'react'
import { CoinApi } from '../api'
import CoinMarketsComponent from '../Components/CoinMarketsComponent'

const CoinMarkets = (props) => {
    const [state, setState] = useState({ loading: true, error: null, data: null })
    const { path } = props.match
    useEffect(() => {
        const getCoin = async () => {
            try {
                const id = path.split('/')[2]
                const { data } = await CoinApi.marketsById(id)
                setState(prev => { return { ...prev, data, loading: false } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getCoin()
    }, [path])
    const { loading, error, data } = state
    return (
        <CoinMarketsComponent loading={loading} error={error} preData={data} />
    )
}

export default CoinMarkets


