import React, { useState, useEffect } from 'react'
import ExchangesComponent from '../Components/ExchangesComponent'
import { CoinApi } from '../api'

const Exchanges = () => {
    const [state, setState] = useState({ loading: true, error: null, data: null })
    useEffect(() => {
        const getCoin = async () => {
            try {
                const { data } = await CoinApi.exchanges()
                setState(prev => { return { ...prev, data, loading: false } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getCoin()
    }, [])
    const { loading, error, data } = state
    return (
        <ExchangesComponent loading={loading} error={error} preData={data} />
    )
}

export default Exchanges

