import React, { useState, useEffect } from 'react'
import { CoinApi } from '../api'
import CoinsComponent from '../Components/CoinsComponent'

const Coins = () => {
    const [state, setState] = useState({ loading: true, error: null, data: null })
    useEffect(() => {
        const getCoin = async () => {
            try {
                const { data: first } = await CoinApi.coins()
                const firstSortedData = first.sort((a, b) => { return a.rank - b.rank })
                const data = firstSortedData.filter(i => i.rank !== 0)
                setState(prev => { return { ...prev, data, loading: false } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getCoin()
    }, [])
    const { loading, error, data } = state
    return (
        <CoinsComponent loading={loading} error={error} preData={data} />
    )
}

export default Coins