import React, { useState, useEffect } from 'react'
import { CoinApi } from '../api'
import CoinExchangesComponent from '../Components/CoinExchangesComponent'

const CoinExchanges = (props) => {
    const [state, setState] = useState({ loading: true, error: null, data: null })
    const { path } = props.match
    useEffect(() => {
        const getCoin = async () => {
            try {
                const id = path.split('/')[2]
                const { data } = await CoinApi.exchangesById(id)
                setState(prev => { return { ...prev, data, loading: false } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getCoin()
    }, [path])
    const { loading, error, data } = state
    return (
        <CoinExchangesComponent loading={loading} error={error} preData={data} />
    )
}

export default CoinExchanges

