import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import Loading from './Loading'

const Container = styled.div`
    border:1px solid yellow;
    max-width:90%;
    margin: 0 auto;
    text-align:center;
    @media screen and (max-width:768px){
    max-width:90%;
    }
`
const Box = styled.div`
    border:1px solid yellow;
    color:white;
    padding:10px;
    font-size:25px;
    @media screen and (max-width:768px){
        font-size:20px;
    }
`
const Title = styled.div`
    font-size:27px;
    font-weight:700;
    padding:10px;
    color:black;
    @media screen and (max-width:768px){
        padding:3px;
        font-size:22px;
    }

`
const PayOn = styled.div`
    padding:10px;
    @media screen and (max-width:768px){
        font-size:18px;
        font-weight:500;
        padding:3px;
    }
`

const CoinExchangesComponent = ({ preData, error, loading }) => {
    const [isFetchMore, setIsFetchMore] = useState(false)
    const [preSet, setPreSet] = useState([])
    const [data, setData] = useState([])

    useEffect(() => {
        setPreSet(preData)
        const filterdData = preSet && preSet.filter((i, index) => index < 15 && i)
        setData(filterdData)
    }, [preData, preSet])

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY + document.documentElement.clientHeight > document.documentElement.scrollHeight - 20) {
                if (preSet.length <= data.length) {
                    return
                }
                else {
                    const filterdData = preSet && preSet.filter((i, index) => index < (data.length + 15) && i)
                    setIsFetchMore(true)
                    setData(filterdData)
                }
            }
        }
        window.addEventListener('scroll', handleScroll)
        return () => window.removeEventListener('scroll', handleScroll)
    }, [preSet, data])
    return (
        loading ? <Loading message="I'm the Coin Exchanges!" top={'-100px'} size={'3em'} /> : error ? <Loading message="You've got the Error!!" /> :
            <>{(data && data.length > 0 && data.map((i, index) => (
                <Container key={index}>
                    <Box>
                        <Title>
                            {i.name}
                        </Title>
                        <PayOn>
                            {i.fiats && i.fiats.length === 0 && <span>Nothing.</span>}
                            {i.fiats && i.fiats.length > 0 && <span style={{ fontWeight: 500, color: 'white' }}>Pay On</span>}
                            {i.fiats && i.fiats.length > 0 && i.fiats.map((i, index) => <span key={index} style={{ color: 'yellow', fontWeight: 700 }}> {i.symbol} </span>)}
                        </PayOn>
                    </Box>
                </Container>

            )))}
                {isFetchMore ? <Loading top='-100px' size='3em' message="I'm the Infinity Scroll!!s" /> : null}
            </>


    )
}

//name,fiats
CoinExchangesComponent.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}
export default CoinExchangesComponent