import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import Loading from './Loading'

const Container = styled.div`
    border:1px solid yellow;
    max-width:100%;
    margin: 0 auto;
    text-align:center;
    display:grid;
    grid-template-columns: repeat(auto-fill,25%);
    @media screen and (max-width:768px){
    grid-template-columns: repeat(auto-fill,50%);
    max-width:90%;
    }
`
const Name = styled.p`
    color:white;
    padding:10px 0;
    border:1px solid yellow;
    font-size:20px;
    a{
        color:inherit;
        cursor: pointer;
    }
    @media screen and (max-width:768px){
        font-size:20px;
    }
`


const CoinMarketsComponent = ({ preData, error, loading }) => {
    const [isFetchMore, setIsFetchMore] = useState(false)
    const [preSet, setPreSet] = useState([])
    const [data, setData] = useState([])

    useEffect(() => {
        setPreSet(preData)
        const filterdData = preSet && preSet.filter((i, index) => index < 30 && i)
        setData(filterdData)
    }, [preData, preSet])

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY + document.documentElement.clientHeight > document.documentElement.scrollHeight - 20) {
                if (preSet.length <= data.length) {
                    return
                }
                else {
                    const filterdData = preSet && preSet.filter((i, index) => index < (data.length + 30) && i)
                    setIsFetchMore(true)
                    setData(filterdData)
                }
            }
        }
        window.addEventListener('scroll', handleScroll)
        return () => window.removeEventListener('scroll', handleScroll)
    }, [preSet, data])
    return (
        loading ? <Loading message="I'm the Coin Markets!" top={'-100px'} size={'3em'} /> : error ? <Loading message="You've got the Error!!" /> :
            (<>
                <Container>
                    {data && data.length > 0 && data.map((i, index) => <Name key={index}><a target="_blank" rel="noopener noreferrer" href={i.market_url}>{i.exchange_name}</a></Name>)}
                </Container>
                {isFetchMore ? <Loading top='-100px' size='3em' message="I'm the Infinity Scroll!!s" /> : null}
            </>
            )
    )
}


CoinMarketsComponent.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}
export default CoinMarketsComponent