import React from 'react'
import styled from 'styled-components'

const Loading = styled.div`
h1 {
    position: relative;
    color: rgba(0, 0, 0, .3);
    top:${props => props.top ? props.top : 'unset'};
    font-size: ${props => props.size ? props.size : '5em'};
    line-height:5em;
    text-align:center;
}
h1:before {
    content: attr(data-text);
    line-height:5em;
    max-width:100%;
    position: absolute;
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
    animation: loading 5s linear infinite;
}
@keyframes loading {
    0% {
        max-width: 0;
    }
}
@media screen and (max-width:768px){
    margin:0;
    padding:0;
    top:-20px;
    position: relative;
    h1{
    top:unset;
    font-size:23px;
    line-height:30px;
    }
    h1:before{
    line-height:30px;
    }
}
`

export default ({ top, size, message }) => {
    return (
        <>
            <Loading top={top} size={size}><br /><br />
                <h1 data-text={message}>{message}</h1>
            </Loading >
        </>
    )
}