import React, { useState, useEffect } from 'react'
import { CoinApi } from '../api'
import PricesComponent from '../Components/PricesComponent'

const Prices = () => {
    const [state, setState] = useState({ loading: true, error: null, data: null })
    useEffect(() => {
        const getCoin = async () => {
            try {
                const { data: sort } = await CoinApi.homePage()
                const data = sort.sort((a, b) => { return a.rank - b.rank })
                setState(prev => { return { ...prev, data, loading: false } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getCoin()
    }, [])
    const { loading, error, data: preData } = state
    return (
        <PricesComponent loading={loading} error={error} preData={preData} />
    )
}

export default Prices