import React, { useState, useEffect } from "react";
import Loading from "./Loading";
import PropTypes from 'prop-types'
import styled from 'styled-components'

const Container = styled.ul`
    max-width:50%;
    margin: 0 auto;
    padding-top: 50px;
    @media screen and (max-width:768px){
    padding-top: 10px;
    max-width:100%;
    }
`
const ListItem = styled.li`
    font-size:18px;
    color:white;
    padding:10px 0;
    justify-content:center;
    width:100%;
    margin:auto;
    @media screen and (max-width:768px){
        width:90%;
        margin:auto
    }
`

const Contents = styled.div`
    text-align:center;
    padding:20px;
    background:rgba(255,255,255,0.4);
    font-weight:500;
    color:white;
    span{
        cursor: pointer;
        color:black;
        font-size:13px;
    }
    &:first-child{
        color:black;
        padding:10px 0;
        font-weight:700;
        background-color: beige;
    }
    &:nth-child(2){
        cursor:pointer;
        color:greenyellow;
    }
    &:nth-child(3){
        border-top:2px solid red;
        padding: 10px 0;
        a{
        display:block;
        cursor: pointer;
        color: #baf8ba ;
        text-decoration:none;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    @media screen and (max-width:768px){
        font-size:15px;
        padding:0;
        &:first-child{
        color:black;
        padding:10px 0;
        font-weight:700;
        background-color: beige;
    }
    &:nth-child(2){
        color:greenyellow;
    }
    &:nth-child(3){
        border-top:2px solid red;
        padding: 10px 0;
        a{
        display:block;
        cursor: pointer;
        color:blue;
    }
    }
    }}
`
const ExchangesComponent = ({ loading, error, preData }) => {
    const [clicked, setClicked] = useState(null)
    const [isFetchMore, setIsFetchMore] = useState(false)
    const [preSet, setPreSet] = useState([])
    const [data, setData] = useState([])

    useEffect(() => {
        setPreSet(preData)
        const filterdData = preSet && preSet.filter((i, index) => index < 10 && i)
        setData(filterdData)
    }, [preData, preSet])

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY + document.documentElement.clientHeight > document.documentElement.scrollHeight - 20) {
                if (preSet.length <= data.length) {
                    return
                }
                else {
                    const filterdData = preSet && preSet.filter((i, index) => index < (data.length + 10) && i)
                    setIsFetchMore(true)
                    setData(filterdData)
                }
            }
        }
        window.addEventListener('scroll', handleScroll)
        return () => window.removeEventListener('scroll', handleScroll)
    }, [preSet, data])

    const onClick = (e) => {
        e.preventDefault()
        const id = e.target.id
        if (data[id].description && clicked !== id) {
            setClicked(id)
            return document.getElementById(id).innerHTML = data[id].description + `<span> Click for shrink</span`
        }
        if (data[id].description && clicked === id) {
            document.getElementById(clicked).innerHTML = descriptionVisual(data[clicked]) + `<span> More</span>`
            setClicked(null)
        }
    }
    const descriptionVisual = (i) => {
        if (i.description.length > 90) {
            return `${i.description.substring(0, 90)}... `
        }
        return i.description
    }
    return (
        <>
            {loading ? <Loading message="Exchanges Loading..." /> : error ? <Loading message="You've got the Error!!" /> :
                <>
                    <Container>{
                        data && data.length > 0 && data.map((i, index) =>
                            <ListItem key={index}>
                                <Contents>{i.name}</Contents>
                                <Contents id={index} onClick={onClick}>
                                    {i.description ? descriptionVisual(i) : 'No description!'}{i.description && i.description.length > 90 && <span> More</span>}
                                </Contents>
                                <Contents>{i.links.website && i.links.website.map((i, index) => <a target="_blank" rel="noopener noreferrer" key={index} href={i}>{i}</a>)}</Contents>
                            </ListItem>)
                    }</Container>
                    {isFetchMore ? <Loading top='-100px' size='3em' message="I'm the Infinity Scroll!!s" /> : null}
                </>
            }
        </>
    )
}

ExchangesComponent.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}
export default ExchangesComponent