import React, { useState, useEffect } from 'react'
import { CoinApi } from '../api'
import CoinDetailComponent from '../Components/CoinDetailComponent'

const CoinDetail = (props) => {
    const [state, setState] = useState({ loading: true, error: null, data: null })
    const { match: { params: { id } } } = props
    useEffect(() => {
        const getCoin = async () => {
            try {
                const { data } = await CoinApi.coinById(id)
                setState(prev => { return { ...prev, data, loading: false } })
            } catch (error) {
                setState(prev => { return { ...prev, error, loading: false } })
            }
        }
        getCoin()
    }, [id])
    const { loading, error, data } = state
    return (
        <CoinDetailComponent loading={loading} error={error} data={data} />
    )
}

export default CoinDetail
